




























import fIcon from '@/components/m-icon/f-icon.vue'
import {mapState, mapGetters, mapMutations} from 'vuex'

export default {
	components: {fIcon},
	name: 'user-swtich',
	props: {
		src: { //图片地址
		    type: String,
		    default: ''
		},
	    show: { //是否显示模态框
	        type: [Boolean, String],
	        default: false
	    }
	},
	data() {
		return {
			isOpen: false,
			showAnimation:false,
			inWatchTimeout:false,
			inMethodsTimeout:false,
			role:{
				self:'自己',
				children:'子女',
				friend:'朋友',
				parent:'父母',
				everybody:'红娘'
			}
		};
	},
	computed: {
		...mapState(['account']),
		...mapGetters(['agentMember'])
	},
	watch: {
	    show(val) {
	        if(this.inWatchTimeout){
	            return
	        }
	        if (val) {
	        	this.isOpen = val
	            setTimeout(() => {
	            	this.showAnimation = val
	            }, 50)
	        } else {
	            this.showAnimation = val
	            this.inWatchTimeout = true
	        	setTimeout(() => {
	        		this.isOpen = val
	                this.inWatchTimeout = false
	        	}, 200)
	        }
	    }
	},
	created() {
		this.isOpen = this.show
		setTimeout(() => {
			this.showAnimation = this.show
		}, 50)
	},
	methods:{
		...mapMutations(['changeAgentMember']),
		closeModal(changed) {
		    this.showAnimation = false
		    this.inMethodsTimeout = true
		    setTimeout(() => {
		    	this.isOpen = false
		        this.inMethodsTimeout = false
		        this.$emit('close', changed);
		    }, 200)
		},
		doSwitch:function(member){
			if(member.id === this.agentMember.id)return;
			this.changeAgentMember(member);
			this.closeModal(true);
		},
		addMember:function(){
			this.closeModal(true);
			uni.switchTab({
				url:'/pages/blind/blind'
			})
		}
	},
	onShow() {
		
	},
	onLoad() {
		
	}
}
